<template>
  <div class="cart">
    <Navbar />
    <h1 class="cart-title text-center m-4">
      Корзина
    </h1>
    <!-- Наименование и стоимость -->
    <div class="container mt-3">
      <div class="d-flex flex-row justify-content-center">
        <div class="col-xl-2 col-lg-2 col-md-3 d-none d-md-block p-0 mr-5">
          <div class="text-center banner1 p-0 m-0">
            <router-link to="/sales">
              <img
                src="@/img/banner/153x515.png"
                class="img-fluid"
                style="height: 666px; width: 200px;"
                alt
              >
            </router-link>
          </div>
        </div>
        <div class="col-xl-7 col-lg-7 col-md-9 col-sm-12 col-12 p-2">
          <div class="row">
            <div
              class="col-xl-12 col-lg-12 col-md-12 col-sm-12 p-0 border-top border-left border-right text-center"
            >
              <div class="border-bottom pt-2 d-flex justify-content-between">
                <h4 class="cart-product__title ml-5">
                  Наименование
                </h4>
                <h4 class="cart-product__title mr-xl-4 mr-lg-4 mr-md-3">
                  Стоимость
                </h4>
              </div>
              <div style="overflow-y: scroll; height:500px;">
                <div v-if="$store.state.cart.length > 0">
                  <div
                    v-for="item in $store.state.cart"
                    :key="item.id"
                    class="media text-left border-bottom"
                  >
                    <div
                      style=" cursor: pointer"
                      class="delprod"
                      @click.prevent="removeFromCart(item)"
                    >
                      <a
                        style="text-decoration: none !important;  color: red !important"
                        href="#"
                      >X</a>
                    </div>
                    <b-img
                      :src="`http://bohmann.shop${item.images[0]}`"
                      class="align-self-center cart-img m-2"
                      alt="..."
                      style=" cursor: pointer"
                      @click="goToproduct(item.id)"
                    />
                    <div class="media-body align-self-center">
                      <h5 class="mt-0 mb-2 mr-5 cart-product__name">
                        <a
                          href="#"
                          @click="goToproduct(item.id)"
                        >
                          {{
                            item.name
                          }}
                        </a>
                      </h5>
                      <div class="mb-0 cart-product__category d-flex align-items-center">
                        <button
                          :disabled="item.quantity <= 1"
                          class="qua_count basket_min"
                          @click="delToCart(item)"
                        >
                          -
                        </button>
                        <input
                          v-model="item.quantity"
                          class="text-center"
                          style="width:40px; height:40px cursor: pointer"
                          readonly
                        >
                        <a
                          style="cursor: pointer"
                          class="qua_count"
                          @click="addToCart(item)"
                        >+</a>
                      </div>
                    </div>

                    <div
                      class="d-flex border-left align-items-center justify-content-center cart-product__price text-center pl-4 pr-4"
                    >
                      {{ item.totalPrice }}
                      <span class="money">грн</span>
                    </div>
                  </div>
                </div>
                <div
                  v-else
                  class="text-center no-prod"
                >
                  Корзина пуста :(
                </div>
              </div>

              <div class="media text-left border-top border-bottom">
                <div class="media-body align-self-center">
                  <h3 class="mt-0 mb-3 ml-3 mr-5 mд-5 cart-product__name">
                    Итоговая стоимость:
                  </h3>
                </div>
                <div
                  class="d-flex border-left align-items-center justify-content-center cart-product__price text-center pl-4 pr-4"
                >
                  {{ totalPrice }} грн
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="col-xl-2 col-lg-2 d-none d-lg-block ml-5">
          <AccountWindow />
        </div>
      </div>
    </div>
    <!-- ./Наименование и стоимость -->
    <!-- Помощь -->
    <div class="container mt-5">
      <div class="d-flex flex-row align-items-center">
        <div class="col-xl-2 col-lg-2 col-md-3 d-none d-md-block p-0 mr-5">
          <div class="text-center banner1 p-0 m-0">
            <router-link to="/sales">
              <img
                src="@/img/banner/153х800.png"
                class="img-fluid"
                style="height: 819px; width: 200px;"
                alt
              >
            </router-link>
          </div>
        </div>
        <div class="col-xl-7 col-lg-7 col-md-9 col-sm-12 col-12 border p-0">
          <div class="media p-4 border-bottom">
            <img
              src="@/img/help.svg"
              class="align-self-center help-img mr-3"
              alt="help"
            >
            <div class="media-body align-self-center">
              <h5 class="mt-0 d-inline">
                Помощь с заказом
              </h5>
            </div>
            <button
              v-if="log === 'true'"
              v-b-modal.help-modal-center
              type="button"
              class="align-self-center btn btn-light border help-b"
              @click="helpButton"
            >
              {{ labelTextButton }}
            </button>
          </div>

          <b-modal
            id="help-modal-center"
            ref="my-modal3"
            v-model="modalShow3"
            hide-footer
            ok-only
            centered
            title="Опишите свою проблему"
          >
            <b-form>
              <b-form-group
                type="email"
                label="Ваша проблема:"
              >
                <b-form-textarea
                  id="textarea"
                  v-model="helpText"
                  rows="4"
                  max-rows="7"
                />
              </b-form-group>
            </b-form>
            <b-button
              block
              type="submit"
              class="mt-3 btn btn-light border help-b"
              @click="sendProblem"
            >
              Отправить
            </b-button>
          </b-modal>

          <form class="p-3">
            <div class="form-group">
              <label for="exampleFormFIO">Имя</label>
              <input
                v-model="user.name"
                type="text"
                class="form-control"
              >
            </div>
            <div class="form-group">
              <label for="exampleFormFIO">Фамилия</label>
              <input
                v-model="user.last_name"
                type="text"
                class="form-control"
              >
            </div>
            <div class="form-group">
              <label for="exampleFormFIO">Отчество</label>
              <input
                v-model="user.fathers"
                type="text"
                class="form-control"
              >
            </div>
            <div class="form-group">
              <label for="exampleFormEmail">Email</label>
              <input
                v-model="user.email"
                type="email"
                class="form-control"
              >
            </div>
            <div class="form-group">
              <label for="exampleFormPhone">Телефон</label>
              <input
                v-model="user.username"
                type="text"
                class="form-control"
              >
            </div>
            <div class="form-group">
              <label for="exampleFormCity">Город</label>
              <input
                v-model="user.city"
                type="text"
                class="form-control"
              >
            </div>
            <div class="form-group">
              <label for="exampleFormBranch">Отдел НП</label>
              <input
                v-model="postOffice"
                type="text"
                class="form-control"
              >
            </div>
            <div class="form-group">
              <label for="exampleFormText">Коментарий к заказу</label>
              <textarea
                v-model="comment"
                class="form-control"
                rows="3"
              />
            </div>
            <div class="form-inline justify-content-end">
              <div class="form-group">
                <div>
                  <b-button
                    v-b-modal.modal-6
                    variant="success"
                    class="ml-2"
                    @click="orderButton"
                  >
                    Подтвердить
                  </b-button>

                  <b-modal
                    id="modal-6"
                    ref="my-modal4"
                    v-model="modalShow4"
                    hide-footer
                    centered
                    title="Спасибо за покупку!"
                    ok-only
                  >
                    <p class="my-4">
                      Ваш заказ принят.
                    </p>
                    <b-button
                      block
                      type="submit"
                      variant="success"
                      @click="sendOrder"
                    >
                      Отправить
                    </b-button>
                  </b-modal>
                </div>
              </div>
            </div>
          </form>
        </div>
        <div class="col-xl-2 col-lg-2 d-none d-lg-block ml-5 p-0">
          <div class="text-center banner1 p-0 m-0">
            <router-link to="/sales">
              <img
                src="@/img/banner/185x670.png"
                class="img-fluid"
                style="height: 819px; "
                alt
              >
            </router-link>
          </div>
        </div>
      </div>
    </div>
    <!-- ./Помощь -->
    <Footer />
  </div>
</template>

<script>
import Navbar from "./Navbar.vue";
import Footer from "./Footer.vue";
import axios from "axios";
import AccountWindow from "./AccountWindow.vue";

export default {
    name: "Baskt",
    components: {
        Navbar,
        Footer,
        AccountWindow
    },
    props: ["user"],
    data: function() {
        return {
            sup: {},
            ord: {},
            modalShow3: false,
            modalShow4: false,
            labelTextButton: "Оформить",
            helpText: "",
            counter: 1,
            disableButton: true,
            log: this.$store.state.login,
            name: "",
            last_name: "",
            fathers: "",
            email: "",
            telephone: "",
            city: "",
            postOffice: "",
            comment: ""
        };
    },
    computed: {
        totalPrice() {
            let total = 0;

            for (let item of this.$store.state.cart) {
                total += item.totalPrice;
            }
            return total.toFixed(0);
        }
    },
    methods: {
        helpButton() {
            this.modalShow3 = !this.modalShow3;
            axios
                .post("http://bohmann.shop/api/tickets/", {
                    user: this.user.id,
                    name: "Помогите"
                })
                .then(response => (this.sup = response.data.id));
        },
        sendProblem() {
            axios.post(`http://bohmann.shop/api/tickets/${this.sup}/messages/`, {
                text: this.helpText,
                answer: false
            });
            this.$refs["my-modal3"].hide();
            this.helpText = "";
            this.labelTextButton = "Ваша заявка принята на рассмотрение";
        },
        orderButton() {
            this.modalShow4 = !this.modalShow4;
            axios
                .post("http://bohmann.shop/api/orders/", {
                    user: this.user.id,
                    name: this.user.name,
                    last_name: this.user.last_name,
                    fathers: this.user.fathers,
                    email: this.user.email,
                    telephone: this.user.username,
                    city: this.user.city,
                    postOffice: this.postOffice,
                    comment: this.comment
                })
                .then(response => (this.ord = response.data.id));
        },
        sendOrder() {
            this.$refs["my-modal4"].hide();
            this.$store.state.cart = [];
            this.$store.state.cartCount = 0;
            this.$store.commit("saveCart");
            for (let i = 0; i <= this.$store.state.cart.length; i++) {
                axios.post(`http://bohmann.shop/api/productsInOrder/`, {
                    product: this.$store.state.cart[i].id,
                    order: this.ord,
                    count: this.$store.state.cart[i].quantity
                });
            }
            window.location.reload();
        },
        goToproduct(Id) {
            this.$router.push({ name: "product", params: { Pid: Id } });
        },
        delToCart(item) {
            this.$store.commit("delToCart", item);
        },
        addToCart(item) {
            this.$store.commit("addToCart", item);
        },
        removeFromCart(item) {
            this.$store.commit("removeFromCart", item);
        }
    }
};
</script>

<style></style>
